<template>
  <div>
    <!-- 面包屑导航部分 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item :to="{ path: '/users' }">用户管理</el-breadcrumb-item>
      <el-breadcrumb-item>用户列表</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片内容部分 -->
    <el-card>
      <el-row :gutter="20">
        <el-col :span="6">
          <!-- 搜索用户功能 -->
          <!-- 优化，搜索栏小x清除搜索的数据后自动请求最新数据 -->
          <el-input v-model="pageList.query" placeholder="搜索用户" prefix-icon="el-icon-search" clearable @clear="getUsersList">
            <!-- 搜索直接调用方法即可，query已经绑定 -->
            <el-button slot="append" icon="el-icon-search" @click="getUsersList"></el-button>
          </el-input>
        </el-col>
        <el-col :span="8">
          <el-button type="primary" icon="el-icon-user" @click="addDialogVisible = true">新增用户</el-button>
        </el-col>
      </el-row>
      <!-- 用户列表展示部分 -->
      <el-table :data="users" stripe style="width: 100%" border>
        <el-table-column type="index" label="序号">
        </el-table-column>
        <el-table-column prop="username" label="用户名">
        </el-table-column>
        <el-table-column prop="email" label="邮箱地址">
        </el-table-column>
        <el-table-column prop="mobile" label="联系电话">
        </el-table-column>
        <el-table-column prop="role_name" label="角色">
        </el-table-column>
        <el-table-column label="激活状态">
          <template slot-scope="scope">
            <el-switch v-model="scope.row.mg_state" active-color="#13ce66" inactive-color="#ff4949" @change="userActive(scope.row)">
            </el-switch>
          </template>
        </el-table-column>
        <!-- 操作用户信息部分 -->
        <el-table-column label="操作" width="300px">
          <template slot-scope="scope">
            <!-- 编辑用户按钮 -->
            <el-button type="primary" icon="el-icon-edit" @click="editorDialog(scope.row.id)" size="mini">编辑</el-button>
            <!-- 删除用户按钮 -->
            <el-button type="danger" icon="el-icon-delete" @click="delUser(scope.row.id)" size="mini">删除</el-button>
            <!-- 设置用户权限按钮 -->
            <el-button type="warning" icon="el-icon-setting" size="mini" @click="setUserRoleDialog(scope.row)">分配角色</el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- 添加用户窗口 -->
      <el-dialog title="添加用户" :visible.sync="addDialogVisible" width="50%" @close="addClose">
        <el-form :model="addUserForm" :rules="addFormRules" ref="addFormRef" label-width="100px">
          <el-form-item label="用户名称" prop="username">
            <el-input v-model="addUserForm.username" suffix-icon="el-icon-user"></el-input>
          </el-form-item>
          <el-form-item label="用户密码" prop="password">
            <el-input v-model="addUserForm.password" placeholder="请输入密码" show-password></el-input>
          </el-form-item>
          <el-form-item label="重复密码" prop="checkPass">
            <el-input v-model="addUserForm.checkPass" placeholder="请再次输入密码" show-password></el-input>
          </el-form-item>
          <el-form-item label="用户邮箱" prop="email">
            <el-input v-model="addUserForm.email" suffix-icon="el-icon-s-comment"></el-input>
          </el-form-item>
          <el-form-item label="联系电话" prop="mobile">
            <el-input v-model="addUserForm.mobile" suffix-icon="el-icon-phone-outline"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="addClose">取 消</el-button>
          <el-button type="primary" @click="addUser">确 定</el-button>
        </span>
      </el-dialog>
      <!-- 编辑用户窗口 -->
      <el-dialog title="编辑用户" :visible.sync="editorDialogVisible" width="50%" @close="editorClose">
        <el-form :model="editorUserForm" :rules="addFormRules" ref="editorFormRef" label-width="100px">
          <el-form-item label="用户名称" prop="username">
            <el-input v-model="editorUserForm.username" suffix-icon="el-icon-user" :disabled="true"></el-input>
          </el-form-item>
          <el-form-item label="用户邮箱" prop="email">
            <el-input v-model="editorUserForm.email" suffix-icon="el-icon-s-comment"></el-input>
          </el-form-item>
          <el-form-item label="联系电话" prop="mobile">
            <el-input v-model="editorUserForm.mobile" suffix-icon="el-icon-phone-outline"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="editorClose">取 消</el-button>
          <el-button type="primary" @click="editorUser">确 定</el-button>
        </span>
      </el-dialog>
      <!-- 分配用户角色窗口 -->
      <el-dialog title="分配用户权限" :visible.sync="setUserRoleDialogVisible" width="50%" @close="setRoleClose">
        <div>
          <p>当前用户:&nbsp;&nbsp;&nbsp;{{setRoleUserInfo.username}}</p>
          <p>当前角色:&nbsp;&nbsp;&nbsp;{{setRoleUserInfo.role_name}}</p>
          <p>分配新角色&nbsp;&nbsp;&nbsp;
            <el-select v-model="userRoles" placeholder="请选择新角色">
              <el-option v-for="item in rolesList" :key="item.id" :label="item.roleName" :value="item.id">
              </el-option>
            </el-select>
          </p>
        </div>
        <span slot="footer" class="dialog-footer">
          <el-button @click="setRoleClose">取 消</el-button>
          <el-button type="primary" @click="setRole">确 定</el-button>
        </span>
      </el-dialog>
      <!-- page分页功能部分 -->
      <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pageList.pagenum" :page-sizes="[10, 5, 3, 1]"
        :page-size="pageList.pagesize" layout="total, sizes, prev, pager, next, jumper" :total="total">
      </el-pagination>

    </el-card>
  </div>
</template>
<script>
export default {
  data () {
    // 密码验证器
    var validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入密码'))
      } else {
        if (this.addUserForm.checkPass !== '') {
          this.$refs.addFormRef.validateField('checkPass')
        }
        callback()
      }
    }
    // 密码验证器
    var validatePass2 = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请再次输入密码'))
      } else if (value !== this.addUserForm.password) {
        callback(new Error('两次输入密码不一致!'))
      } else {
        callback()
      }
    }
    // 手机号的正则验证器
    var validateMobilie = (rule, value, callback) => {
      var regtel = /^(13[0-9]|14[01456879]|15[0-3,5-9]|16[2567]|17[0-8]|18[0-9]|19[0-3,5-9])\d{8}$/
      if (value === '') {
        callback(new Error('手机号不能为空'))
      } else if (!regtel.test(value)) {
        callback(new Error('请输入正确的手机号码!'))
      } else {
        callback()
      }
    }
    // 对象属性一定要用对象.方法调用
    return {
      // 用户总数量
      total: 0,
      // 分页的数据
      pageList: {
        query: '',
        // 总页数
        pagenum: 1,
        // 当前页显示数量
        pagesize: 5
      },
      // 首屏获取到的所有数据
      users: [],
      // 添加用户窗口显示初始化
      addDialogVisible: false,
      // 编辑用户
      editorDialogVisible: false,
      // 设置用户角色
      setUserRoleDialogVisible: false,
      // 添加用户的表单数据
      addUserForm: {
        username: '',
        password: '',
        checkPass: '',
        email: '',
        mobile: ''
      },
      // 编辑用户时的表单数据
      editorUserForm: {},
      // 设置用户角色时的表单数据
      setUserForm: {},
      // 已有角色的列表
      rolesList: [],
      // 给用户分配角色的值
      userRoles: '',
      // 需要分配角色的用户信息
      setRoleUserInfo: {},
      // 选择设置的角色
      // selectedValue = 0,
      // 编辑和添加用户时的表单校验
      addFormRules: {
        username: [
          { required: true, message: '请输入用户名称', trigger: 'blur' },
          { min: 3, max: 8, message: '长度在 3 到 8 个字符', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '请输入密码' },
          { validator: validatePass, trigger: 'blur' },
          { min: 6, max: 15, message: '长度在 6 到 15 个字符', trigger: 'blur' }
        ],
        checkPass: [
          { required: true, message: '请再次输入密码' },
          { validator: validatePass2, trigger: 'blur' },
          { min: 6, max: 15, message: '长度在 6 到 15 个字符', trigger: 'blur' }
        ],
        email: [
          { required: true, message: '请输入邮箱地址', trigger: 'blur' },
          { type: 'email', message: '请输入正确的邮箱地址', trigger: ['blur', 'change'] }
        ],
        mobile: [
          { required: true, message: '请输入邮箱地址', trigger: 'blur' },
          { validator: validateMobilie, trigger: 'blur' }
        ]

      }
    }
  },
  // 首屏数据
  created () {
    this.getUsersList()
  },
  methods: {
    // 获取所有的用户
    async getUsersList () {
      const { data: res } = await this.$http.get('users', { params: this.pageList })
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      this.total = res.data.total
      this.users = res.data.users
    },
    // 用户状态变更
    async userActive (userinfo) {
      const { data: res } = await this.$http.put(`/users/${userinfo.id}/state/${userinfo.mg_state}`)
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      this.$message.success(res.meta.msg)
    },
    // 添加用户
    addUser () {
      // 调用表单的校验方法
      this.$refs.addFormRef.validate(async callback => {
        if (!callback) return this.$message.error('请输入合法的用户信息！！')
        const { data: res } = await this.$http.post('users', this.addUserForm)
        if (res.meta.status !== 201) return this.$message.error(res.meta.msg)
        this.$message.success(res.meta.msg)
        this.addDialogVisible = false
        // 重置表单内容
        // this.$refs.addFormRef.resetFields()
        this.getUsersList()
      })
    },
    // 关闭添加窗口时重置表单
    addClose () {
      this.$refs.addFormRef.resetFields()
      this.addDialogVisible = false
    },
    // 关闭编辑窗口时重置表单
    editorClose () {
      this.$refs.editorFormRef.resetFields()
      this.editorDialogVisible = false
    },
    // 关闭设置权限窗口
    setRoleClose () {
      this.setUserRoleDialogVisible = false
    },
    // 显示单页显示的数量
    handleSizeChange (size) {
      this.pageList.pagesize = size
      this.getUsersList()
    },
    // 翻页功能
    handleCurrentChange (current) {
      this.pageList.pagenum = current
      this.getUsersList()
    },
    // 编辑用户窗口激活并获取需要编辑的用户信息
    async editorDialog (id) {
      this.editorDialogVisible = true
      const { data: res } = await this.$http.get(`users/${id}`)
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      this.editorUserForm = res.data
    },
    // 编辑功能
    editorUser () {
      this.$refs.editorFormRef.validate(async callback => {
        if (!callback) return this.$message.error('请输入合法的用户信息！！')
        const { data: res } = await this.$http.put(`users/${this.editorUserForm.id}`, this.editorUserForm)
        if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
        this.$message.success(res.meta.msg)
        this.editorDialogVisible = false
        // 不需要重置表单内容
        // this.$refs.editorFormRef.resetFields()
        this.getUsersList()
      })
    },
    // 删除用户按钮
    delUser (id) {
      this.$confirm('此操作将永久删除该用户, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(async () => {
        const { data: res } = await this.$http.delete(`users/${id}`)
        if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
        this.$message.success(res.meta.msg)
        // 手动修复element分页删除最后一页最后一条数据时不跳转页码的bug
        const totalPage = Math.ceil((this.total - 1) / this.pageList.pagesize) // 总页数
        this.pageList.pagenum = this.pageList.pagenum > totalPage ? totalPage : this.pageList.pagenum
        this.pageList.pagenum = this.pageList.pagenum < 1 ? 1 : this.pageList.pagenum
        this.getUsersList()
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        })
      })
    },
    // 设置用户权限窗口激活并获取需要设置用户的信息
    async setUserRoleDialog (userinfo) {
      this.setUserRoleDialogVisible = true
      this.setRoleUserInfo = userinfo
      const { data: res } = await this.$http.get('roles')
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      this.rolesList = res.data
      // 不需要过滤
      // res.data.forEach(item => {
      //   this.rolesList.push({ roleName: item.roleName, id: item.id })
      // })
    },
    // 设置用户角色的功能
    async setRole () {
      const { data: res } = await this.$http.put(`users/${this.setRoleUserInfo.id}/role/`, {
        rid: this.userRoles
      })
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      this.$message.success(res.meta.msg)
      this.userRoles = ''
      this.getUsersList()
      this.setUserRoleDialogVisible = false
    }
  }

}
</script>
