<template>
  <div>
    <div>
      <!-- 面包屑导航部分 -->
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/roles' }">权限管理</el-breadcrumb-item>
        <el-breadcrumb-item>角色列表</el-breadcrumb-item>
      </el-breadcrumb>
      <!-- 卡片内容部分 -->
      <el-card>
        <!-- 添加角色按钮 -->
        <el-row>
          <el-button type="primary" icon="el-icon-user" @click="addRoleDialogVisible = true">添加角色</el-button>
        </el-row>
        <!-- 角色列表展示部分 -->
        <el-table :data="rolesList" stripe style="width: 100%" border class="rolesList">
          <el-table-column type="expand" label="展开显示" width="100px">
            <template slot-scope="scope">
              <!-- 第一级权限 是一个大行 -->
              <el-row v-for="(item1,i1) in scope.row.children" :key="item1.id" :class="['bdbottom','vcenter',i1 === 0 ? 'bdtop':'']">
                <el-col :span="5">
                  <el-tag type="" closable @close="removeRights(item1.id,scope.row)">
                    {{item1.authName}}
                  </el-tag>
                </el-col>
                <!-- 渲染二级 -->
                <el-col :span="19">
                  <el-row v-for="(item2,i2) in item1.children" :key="item2.id" :class="i2 === 0 ? 'vcenter':'bdtop vcenter'">
                    <el-col :span='6'>
                      <el-tag type="success" closable @close="removeRights(item2.id,scope.row)">
                        {{item2.authName}}
                      </el-tag>
                    </el-col>
                    <el-col :span='18'>
                      <el-tag type="warning" closable v-for="item3 in item2.children" :key="item3.id" @close="removeRights(item3.id,scope.row)">
                        {{item3.authName}}
                      </el-tag>
                    </el-col>
                  </el-row>
                </el-col>
              </el-row>
            </template>
            <!-- 角色权限列表完毕 -->
          </el-table-column>
          <el-table-column type="index" label="序号" width="50px">
          </el-table-column>
          <el-table-column prop="roleName" label="角色名称">
          </el-table-column>
          <el-table-column prop="roleDesc" label="角色描述">
          </el-table-column>
          <!-- 操作角色信息部分 -->
          <el-table-column label="操作" width="300px">
            <template slot-scope="scope">
              <!-- 编辑角色按钮 -->
              <el-button type="primary" icon="el-icon-edit" @click="editorRoleDialog(scope.row.id)" size="mini">编辑</el-button>
              <!-- 删除角色按钮 -->
              <el-button type="danger" icon="el-icon-delete" @click="delRole(scope.row.id)" size="mini">删除</el-button>
              <!-- 设置角色权限按钮 -->
              <el-button type="warning" icon="el-icon-setting" size="mini" @click="setRightsDialog(scope.row)">分配权限</el-button>
            </template>
          </el-table-column>
        </el-table>
        <!-- 分配角色权限的窗口 -->
        <el-dialog title="分配角色权限" :visible.sync="setRightsDialogVisible" width="50%" @close="setRightsClose">
          <!-- 树形控件 -->
          <el-tree :data="setRightsList" show-checkbox default-expand-all node-key="id" :default-checked-keys="defKeys" ref="setRightsTreeRef" highlight-current
            :props="defaultProps">
          </el-tree>
          <span slot="footer">
            <el-button @click="setRightsClose">取 消</el-button>
            <el-button type="primary" @click="setRights()">确 定</el-button>
          </span>
        </el-dialog>
        <!-- 添加角色的窗口 -->
        <el-dialog title="添加角色" :visible.sync="addRoleDialogVisible" width="50%" @close="addRoleClose">
          <el-form :model="addRoleForm" :rules="addRoleFormRules" ref="addRoleFormRef" label-width="100px">
            <el-form-item label="角色名称" prop="roleName">
              <el-input v-model="addRoleForm.roleName" suffix-icon="el-icon-user"></el-input>
            </el-form-item>
            <el-form-item label="角色描述" prop="roleDesc">
              <el-input v-model="addRoleForm.roleDesc" placeholder="请输入角色描述信息"></el-input>
            </el-form-item>
          </el-form>
          <span slot="footer">
            <el-button @click="addRoleClose">取 消</el-button>
            <el-button type="primary" @click="addRole">确 定</el-button>
          </span>
        </el-dialog>
        <!-- 编辑角色的窗口 -->
        <el-dialog title="编辑角色" :visible.sync="editorRoleDialogVisible" width="50%" @close="editorRoleClose">
          <el-form :model="editorRoleForm" :rules="addRoleFormRules" ref="editorRoleFormRef" label-width="100px">
            <el-form-item label="角色名称" prop="roleName">
              <el-input v-model="editorRoleForm.roleName" suffix-icon="el-icon-user"></el-input>
            </el-form-item>
            <el-form-item label="角色描述" prop="roleDesc">
              <el-input v-model="editorRoleForm.roleDesc" suffix-icon="el-icon-s-comment"></el-input>
            </el-form-item>
          </el-form>
          <span slot="footer">
            <el-button @click="editorRoleClose">取 消</el-button>
            <el-button type="primary" @click="editorRole">确 定</el-button>
          </span>
        </el-dialog>
      </el-card>
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      // 已有角色列表
      rolesList: [],
      // 设置角色多选表单权限的列表
      setRightsList: [],
      // 添加角色的表单信息
      addRoleForm: {},
      // 编辑角色的表单信息
      editorRoleForm: {},
      // 多选树绑定的数据
      defaultProps: {
        children: 'children',
        label: 'authName'
      },
      // 多选结构树所有三级权限的id
      defKeys: [],
      // 要修改权限的id
      rolesId: '',
      // 分配权限的窗口
      setRightsDialogVisible: false,
      // 添加角色的窗口
      addRoleDialogVisible: false,
      // 编辑角色的窗口
      editorRoleDialogVisible: false,
      // 添加角色的校验规则
      addRoleFormRules: {
        roleName: [
          { required: true, message: '请输入角色名称', trigger: 'blur' },
          { min: 3, max: 8, message: '长度在 3 到 8 个字符', trigger: 'blur' }
        ],
        roleDesc: [
          { required: true, message: '请输入角色描述', trigger: 'blur' },
          { min: 3, max: 8, message: '长度在 6 到 20 个字符', trigger: 'blur' }
        ]
      }
    }
  },
  created () {
    this.getRolesList()
  },
  methods: {
    // 获取所有已有的角色列表
    async getRolesList () {
      const { data: res } = await this.$http.get('roles')
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      this.rolesList = res.data
    },
    // 移除标签形式权限的模块
    removeRights (rightId, roles) {
      this.$confirm('此操作将删除该角色的对应权限, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(async () => {
        const { data: res } = await this.$http.delete(`roles/${roles.id}/rights/${rightId}`)
        if (res.meta.status !== 200) return this.$messgae.error(res.meta.msg)
        this.$message.success(res.meta.msg)
        // 不推荐直接调用获取权限方法，会刷新整个页面，使用返回来的值直接给形参，数据会回流到实参上。
        roles.children = res.data
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        })
      })
    },
    // 多选结构树的功能
    async setRightsDialog (roles) {
      this.rolesId = roles.id
      const { data: res } = await this.$http.get('rights/tree')
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      this.setRightsList = res.data
      this.getLeafKeys(roles, this.defKeys)
      this.setRightsDialogVisible = true
    },
    // 关闭分配权限的窗口
    setRightsClose () {
      // 重置选中的三级节点
      this.defKeys = []
      this.setRightsDialogVisible = false
    },
    // 关闭添加角色的窗口
    addRoleClose () {
      this.addRoleDialogVisible = false
      this.$refs.addRoleFormRef.resetFields()
    },
    // 关闭编辑窗口时重置表单
    editorRoleClose () {
      this.$refs.editorRoleFormRef.resetFields()
      this.editorRoleDialogVisible = false
    },
    // 添加角色的模块
    addRole () {
      this.$refs.addRoleFormRef.validate(async callback => {
        if (!callback) return this.$message.error('请输入合法的角色信息！！')
        const { data: res } = await this.$http.post('roles', this.addRoleForm)
        if (res.meta.status !== 201) return this.$message.error(res.meta.msg)
        this.$message.success(res.meta.msg)
        this.addRoleDialogVisible = false
        // 重置表单内容
        // this.$refs.addRoleFormRef.resetFields()
        this.getRolesList()
      })
    },
    // 编辑角色窗口激活并获取需要编辑的角色信息
    async editorRoleDialog (id) {
      this.editorRoleDialogVisible = true
      const { data: res } = await this.$http.get(`roles/${id}`)
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      this.editorRoleForm = res.data
    },
    // 编辑角色功能
    editorRole () {
      this.$refs.editorRoleFormRef.validate(async callback => {
        if (!callback) return this.$message.error('请输入合法的角色信息！！')
        const { data: res } = await this.$http.put(`roles/${this.editorRoleForm.roleId}`, this.editorRoleForm)
        if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
        this.$message.success(res.meta.msg)
        this.editorRoleDialogVisible = false
        // 重置表单内容
        // this.$refs.editorRoleFormRef.resetFields()
        this.getRolesList()
      })
    },
    // 删除角色按钮
    delRole (id) {
      this.$confirm('此操作将永久删除该角色, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(async () => {
        const { data: res } = await this.$http.delete(`roles/${id}`)
        if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
        this.$message.success(res.meta.msg)
        this.getRolesList()
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        })
      })
    },
    // 设置分配权限的功能
    async setRights () {
      // this.$refs.setRightsTreeRef.getHalfCheckedKeys()返回的是数组，通过数组展开变成另外一个数组的每一条数据
      const keys = [...this.$refs.setRightsTreeRef.getCheckedKeys(), ...this.$refs.setRightsTreeRef.getHalfCheckedKeys()]
      const idStr = keys.join(',')
      const { data: res } = await this.$http.post(`roles/${this.rolesId}/rights`, { rids: idStr })
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      this.$message.success(res.meta.msg)
      this.getRolesList()
      this.setRightsDialogVisible = false
    },
    // 通过递归函数，获取角色下所有三级权限的id，并保存到defKeys数组中
    getLeafKeys (node, arr) {
      // 取反，如果没有node.children属性的节点，就是三级权限了
      if (!node.children) return arr.push(node.id)
      // 如果有node.children属性，说明还不是三级权限，还需要再次递归循环遍历数组
      node.children.forEach(item => this.getLeafKeys(item, arr))
    }

  }
}
</script>
<style lang="less" scoped>
.vcenter {
  display: flex;
  align-items: center;
}

.el-tag {
  margin: 5px;
}
.bdbottom {
  border-bottom: 1px solid #ccc;
}
.bdtop {
  border-top: 1px solid #ccc;
}
</style>
